@import url('https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700');
$bodyfont: 'Poppins',
sans-serif;
$headingfont: 'Poppins',
sans-serif;
@import '~@angular/material/theming';

/*Material Theme Colors*/

$primary: mat-palette($mat-blue,
600);
$accent: mat-palette($mat-indigo,
500);
$warn: mat-palette($mat-pink,
500);
$theme: mat-light-theme($primary,
$accent,
$warn);
$background: map-get($theme,
background);
$foreground: map-get($theme,
foreground);
@include mat-core();
$custom-typography: mat-typography-config( $font-family: 'Poppins, sans-serif');
// Override the typography in the core CSS.
@include mat-core($custom-typography);
// Default Theme
@include angular-material-theme($theme);
// Dark Theme

/*Theme Colors*/

$topbar: #2196f3;
$sidebar: #fff;
$sidebar-white: #e5edef;
$sidebar-alt: #f2f6f8;
$bodycolor: #eef5f9;
$headingtext: #455a64;
$bodytext: #67757c;
$sidebar-text: #607d8b;
$sidebar-icons: #99abb4;
$font-16: 16px;
$light-text: #a6b7bf;
$themecolor: #1e88e5;
$themecolor-alt: #3457D3;
$themecolor-dark: #028ee1;

